import React, {useState} from 'react';
import Title from "../../../components/title";
import GridView from "../../../containers/grid-view";
import {KEYS} from "../../../constants/keys";
import {URLS} from "../../../constants/urls";
import {PlusCircle} from "react-feather";
import Form from "../../../containers/form";
import {Input} from "../../../containers/form/components";
import {get} from "lodash"
import {useTranslation} from "react-i18next";


const DrgContainer = () => {
    const [open, setOpen] = useState(false)
    const {t} = useTranslation()
    const columns = [
        {
            title: 'ID',
            key: 'id',
            classnames: '!w-[80px] !min-w-0'
        },
        {
            title: t('Code'),
            key: 'code',
        },
        {
            title: t('Diagnosis code'),
            key: 'diagnosis_codes',
        },
        {
            title: t('Ratio'),
            key: 'ratio',
        },
    ]
    return (
        <div>
            <div className="grid grid-cols-12 items-center">
                <div className="col-span-8">
                    <Title>{t("Клинико-Затратные Группы")}</Title>
                </div>
                <div className="col-span-4 text-right">
                    <button
                        onClick={() => setOpen(true)}
                        className={'inline-flex items-center py-2.5 pl-2.5 pr-5 text-sm font-bold text-primary border border-primary rounded-lg'}>
                        <PlusCircle size={20} className={'mr-1.5'}/>
                        {t("Добавить")}
                    </button>
                </div>
                <div className="col-span-12 mt-8">
                    <GridView
                        ModalBody={(onSubmit, defaultValues = {}) => <Form onSubmit={onSubmit}
                                                                           defaultValues={get(defaultValues,'data.data',{})}>
                            <Input type={'input'} name={'code'} placeholder={t('Code')} label={t('Code')}
                                   params={{required: true}}/>
                            <Input type={'input'} name={'ratio'} property={{type:'number'}} placeholder={t('Ratio')} label={t('Ratio')}
                                   params={{required: true}}/>
                            <Input type={'input'} name={'diagnosis_codes'} placeholder={t('Diagnosis code')}
                                   label={t('Diagnosis code')}/>
                            <button type={'submit'}
                                    className={'w-full p-3.5 rounded-lg bg-primary block w-full text-white font-bold text-center h-12 mt-6'}>
                                {open ? t('Create') : t('Save')}
                            </button>
                        </Form>}
                        openCreateModal={open}
                        setOpenCreateModal={setOpen}
                        hasActionColumn
                        listKey={KEYS.drgs} url={URLS.drgs}
                        columns={columns}/>
                </div>
            </div>
        </div>
    );
};

export default DrgContainer;